<template>
  <div class="breadcrumb p-0 bg-transparent">
    <ul class="breadcrumbList m-0">
      <li
        v-for="(breadcrumb, idx) in breadcrumbList"
        :key="idx"
        @click="routeTo(idx)"
        class="breadcrumbItem"
        :class="{ linked: !!breadcrumb.link }"
      >
        {{ breadcrumb.name }}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Breadcrumb",
  data() {
    return {
      breadcrumbList: [],
    };
  },
  mounted() {
    this.updateList();
  },
  watch: {
    $route() {
      this.updateList();
    },
  },
  methods: {
    routeTo(pRouteTo) {
      if (this.breadcrumbList[pRouteTo].link) {
        this.$router.push(this.breadcrumbList[pRouteTo].link);
      }
    },
    updateList() {
      this.breadcrumbList = this.$route.meta.breadcrumb;
    },
  },
}
</script>
<style lang="scss">
.breadcrumbList {
  display: flex;
  list-style: none;
  padding: 0;
  font-size: font(1);
}
.breadcrumbItem {
  cursor: pointer;
  color: #42b983;
}
.breadcrumbItem:last-child {
  cursor: default;
}
.breadcrumbItem:not(:last-child):after {
  content: ">";
  margin: 5px;
}
</style>